<template>
	<view>
		<text class="loading-text">
			{{loadingType === 0 ? contentText.contentdown : (loadingType === 1 ? contentText.contentrefresh : contentText.contentnomore)}}
		</text>
		<table>
			<thead>
				<tr>
					<th style="width: 5px;">工号</th>
					<th style="width: 200px;">姓名</th>
					<th style="width: 200px;">班次</th>
					<th>打卡日期</th>
					<th style="width: 5px;">打卡时间</th>
				</tr>
			</thead>
			<tbody>
				<tr v-for="(item,index) in QueryResultList" :key='index'>
					<td>{{item.userID}}</td>
					<td>{{item.userName}}</td>
					<td>{{item.workShift}}</td>
					<td>{{item.workDate}}</td>
					<td>{{item.workTime}}</td>
				</tr>
			</tbody>
		</table>
	</view>
</template>

<script>
	var _self, page = 1;
	var len = 0;
	export default {
		data() {
			return {
				key: 0,
				loadingType: 0,
				contentText: {
					contentdown: "上拉显示更多",
					contentrefresh: "正在加载...",
					contentnomore: "没有更多数据了"
				},
				QueryResultList: [],
				totalPage: 1, // 统共页数，默认为1
				currentPage: 1, //当前页数 ，默认为1
				pageSize: 10, // 每页显示数量
			};
		},
		onLoad: function(options) {
			// 产品列表数据默认加载	
			_self = this;
			uni.request({
				url: 'http://www.suitdo.cn/api/login/getattendance',
				method: 'GET',
				data: {
					userid: '00075043',
					startdtime: '2020-7-1',
					enddtime: '2020-7-20',
				},
				success: function(res) {
					_self.QueryResultList = res.data.slice(0, 10);
				},
			});
		},
		methods: {
			// 产品列表数据
			getNewsList: function(pages, list) { //第一次回去数据
				_self = this;
				_self.loadingType = 0;
				uni.showNavigationBarLoading();
				let begin = (page - 1) * _self.pageSize;
				let end = page * _self.pageSize;
				_self.QueryResultList = list.slice(begin, end);
			},
			// 上拉加载
			onReachBottom: function() {
				page++; //每触底一次 page +1
				// console.log(_self.QueryResultList.length);
				if (_self.loadingType != 0) { //loadingType!=0;直接返回
					return false;
				}
				_self.loadingType = 1;
				// console.log(page);
				uni.showNavigationBarLoading(); //显示加载动画
				uni.request({
					url: 'http://www.suitdo.cn/api/login/getattendance',
					method: 'GET',
					data: {
						userid: '00075043',
						startdtime: '2020-7-1',
						enddtime: '2020-7-20',
					},
					success: function(res) {
						let n;
						let len = 0;
						for (n in res.data) {
							if (res.data.hasOwnProperty(n)) {
								len++;
							}
						}
						var temp = Math.ceil(len / _self.pageSize);
						if (temp != null)
							_self.totalPage = temp;
						console.log(_self.totalPage);
						// 计算得0时设置为1
						_self.totalPage = _self.totalPage == 0 ? 1 : _self.totalPage;
						if (page > temp) { //没有数据
							_self.loadingType = 2;
							uni.hideNavigationBarLoading(); //关闭加载动画
							return false;
						} else {
							getNewsList(page, res.data);
						}
						_self.loadingType = 0; //将loadingType归0重置
						uni.hideNavigationBarLoading(); //关闭加载动画
					},
				});
			},


		}


	}
</script>
<style></style>
